import { useState, useEffect } from "react";
import unified from "unified";
import parse from "remark-parse";
import remark2react from "remark-react";
import rehype from "rehype-dom-parse";

export default function useMarkdownPost(src) {
  const [markdown, setMarkdown] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchPosts = async () => {
      let res;

      try {
        res = await fetch(`/posts/${src}`);
      } catch (error) {
        console.error(`Error loading post ${src}}`);
        setLoading(false);
        return { loading };
      }
      if (res.ok) {
        const md = await res.text();
        setMarkdown(md);
      }

      setLoading(false);
    };

    fetchPosts();
  }, [src, loading]);

  if (loading) return { loading };

  const rendered = unified()
    .use(parse)
    .use(remark2react, {
      sanitize: false,
      toHast: {
        handlers: {
          html: (h, node) => {
            // process raw HTML text into HAST so react remark can process it
            const child = unified()
              .use(rehype, { fragment: true })
              .parse(node.value).children;
            return child;
          }
        }
      },
      remarkReactComponents: {
        // img: props => <div>{JSON.stringify(props)}</div>
      }
    })
    .processSync(markdown).contents;

  return { loading, rendered };
}
